<template>
    <div class="admin-info">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>群内管理员消息/弹窗推送</span>
                    <div class="list-operate">
                        <div class="send" @click="handleSubmit">
                            <span>发送</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="data-con">
                <div class="data-list">
                    <div class="data-item">
                        <p>消息类型</p>
                        <i-select v-model="messageType" class="item-select">
                            <i-option v-for="(item, index) in messageList" :key="index" :value="item.key">{{item.title}}</i-option>
                        </i-select>
                    </div>
                    <div v-show="messageType == 1" class="data-item">
                        <p>选择彩种</p>
                        <CheckboxGroup v-model="selectedGame">
                            <Checkbox v-for="(item, index) in gameList" :key="index" :label="item.type">
                                <span>{{item.name}}</span>
                            </Checkbox>
                        </CheckboxGroup>
                    </div>
                    <div class="data-item">
                        <p>消息内容</p>
                        <Input v-model="sendText" type="textarea" class="column-textarea" :rows="5" :autosize="{minRows: 5,maxRows: 5}"></Input>
                    </div>
                </div>
            </div>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>滚动公告</span>
                    <div class="list-operate">
                        <div class="send" @click="handleSend('1')">
                            <span>发布/修改</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="data-con">
                <div class="data-list">
                    <div class="data-item">
                        <p>公告内容</p>
                        <Input v-model="sendNotice" type="textarea" class="column-textarea" :rows="4" :autosize="{minRows: 4,maxRows: 4}"></Input>
                    </div>
                </div>
                <div class="data-list">
                    <div class="data-item">
                        <p>公告列表</p>
                    </div>
                    <div class="data-item">
                        <p class="item-info">是否启用</p>
                        <p class="item-info">公告内容</p>
                        <p class="item-info">发布时间</p>
                        <p class="item-info">操作</p>
                    </div>
                    <div class="data-item" v-for="(item, index) in noticeList" :key="index">
                        <div class="item-info">
                            <switchs :open="item.status" @switch="handleChangeStatus(item)"></switchs>
                        </div>
                        <div class="item-info">
                            <p :title="item.content">{{item.content}}</p>
                        </div>
                        <div class="item-info">
                            <span>{{item.time}}</span>
                        </div>
                        <div class="item-info">
                            <div class="item-button" @click="handleShowEdit(item)">修改</div>
                            <div class="item-button" @click="handleDelete(item)">删除</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-show="editShow" class="article-mask">
            <div v-show="editShow" class="article-content">
                <div class="article-header">
                    <span>修改公告</span>
                    <span class="modal-close" @click="handleCloseModal">X</span>
                </div>
                <div class="article-list">
                    <div class="article-item">
                        <p class="article-item-t">公告内容</p>
                        <i-input v-model="editNotice" type="textarea" class="column-textarea" :rows="8" :autosize="{minRows: 8,maxRows: 8}"></i-input>
                    </div>
                    <div class="article-item">
                        <i-button @click="handleSend('2')">提交</i-button>
                    </div>
                </div>
            </div>
        </div>
        <Modal v-model="showDelete" class-name="vertical-center-modal" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>确认删除</span>
            </p>
            <p style="text-align:center">是否确认删除？</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="showDelete = false">取消</div>
                <div class="enter-ok" @click="handleEnterDelete">确定</div>
            </div>
        </Modal>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
import switchs from '../../component/switch';
export default {
    components: {
        switchs
    },
    data() {
        return {
            showLoading: false,
            id: '',
            showDelete: false,
            editShow: false,
            sendNotice: '',
            editNotice: '',
            noticeList: [],
            selectedGame: [],
            sendText: '',
            messageType: 1,
            messageList: [
                {
                    key: 1,
                    title: '群内管理员消息'
                },
                {
                    key: 2,
                    title: '房间弹窗推送消息'
                }
            ],
            gameList: [],
            isLoading: false
        }
    },
    mounted() {
        let that = this;

        that.getGameList().then(function() {
            that.getData();
        })
    },
    methods: {
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                name: item.GameName,
                                type: item.GameType,
                                key: item.NickName
                            }
                        }).sort((a, b) => a.type - b.type);
                        resolve();
                    }
                    reject();
                })
            })
        },
        handleDelete(item) {
            let that = this;

            that.id = item.id;
            that.showDelete = true;
        },
        handleShowEdit(item) {
            let that = this;

            that.editNotice = item.content;
            that.id = item.id;
            that.editShow = true;
        },
        handleChangeStatus(item) {
            let that = this,
                href = '';

            if (item.status) {
                href = '/api/Setup/CloseArticle';
            } else {
                href = '/api/Setup/OpenArticle';
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: href,
                    params: {
                        articleID: item.id
                    },
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.getData();
                })
            }
        },
        handleEnterDelete() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/DeleteArticleByID',
                    method: 'post',
                    params: {
                        ID: that.id
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }

                    that.showDelete = false;
                    that.isLoading = false;
                    that.getData();
                })
            }
        },
        handleSend(type) {
            let that = this,
                vData = {},
                path = '',
                vParam = {},
                vMethod = '';

            if (!that.sendNotice && type === '1') {
                that.$Message.error('请填写公告内容');
                return;
            }

            if (!that.editNotice && type === '2') {
                that.$Message.error('请填写公告内容');
                return;
            }

            if (type === '1') {
                path = '/api/Setup/AddArticle';
                vMethod = 'post';
                vData['Content'] = that.sendNotice;
                vData['ArticleType'] = 1;
            } else {
                vParam['id'] = that.id;
                path = '/api/Setup/UpdateArticle';
                vMethod = 'post';
                vData['Content'] = that.editNotice;
                vData['ArticleType'] = 1;
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: path,
                    method: vMethod,
                    data: vData,
                    params: vParam
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }

                    that.sendNotice = '';
                    that.editNotice = '';
                    that.editShow = false;
                    that.isLoading = false;
                    that.getData();
                })
            }
        },
        getData() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/GetArticleList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.noticeList = result.map(item => {
                            return {
                                id: item.ID,
                                type: item.Type,
                                content: item.Content,
                                time: item.Time.substr(0,16),
                                status: item.Open
                            }
                        })
                    }
                    that.isLoading = false;
                })
            }
        },
        handleCloseModal() {
            let that = this;
            that.editShow = false;
            that.editNotice = '';
        },
        handleSubmit() {
            let that = this,
                vData = {},
                href = '';

            if (!that.sendText.trim()) {
                that.$Message.error('请输入发送内容');
                return;
            } else {
                vData['Content'] = that.sendText;
            }

            if (that.messageType === 1) {
                if (!that.selectedGame.length) {
                    that.$Message.error('请选择游戏类型');
                    return;
                }
                that.gameList.forEach(item => {
                    vData[item.key] = that.selectedGame.findIndex(opt => {return opt === item.type}) !== -1;
                })
                href = '/api/Setup/AddMeaage';
            } else {
                href = '/api/Setup/AddArticle';
                vData['ArticleType'] = 2;
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: href,
                    method: 'post',
                    data: vData
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }

                    that.sendText = '';
                    that.selectedGame = [];
                    that.isLoading = false;
                })
            }
        }
    },
}
</script>

<style lang="scss" scoped>
.admin-info {
    position: relative;
    .content {
        .list-header {
            width: 100%;
            .list-title {
                justify-content: space-between;

                width: 100%;

                .list-operate {
                    .send {
                        width: 122px;
                        height: 30px;
                        line-height: 30px;

                        // background-color: rgb(215, 215, 215);
                        // border-radius: 4px;
                        // border: solid 1px rgb(188, 188, 188);
                        background-color: #11ad44;
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 255, 158, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e903c;
                        text-align: center;
                        font-weight: 400;
                        cursor: pointer;
                        color: #fff;
                    }
                }
            }
        }

        .data-con {
            .data-list {
                margin-top: 10px;

                border: solid 1px #d7d7d7;
                .data-item {
                    display: flex;
                    align-items: center;

                    padding: 10px;

                    border-bottom: solid 1px #d7d7d7;

                    & > p {
                        padding: 0 15px;

                        width: 70px;
                    }

                    .item-select {
                        width: 280px;

                        text-align: left;
                    }
                }

                .data-item:last-child {
                    border-bottom: 0;
                }
            }
        }
    }

    .content:nth-child(2) {
        .data-con {
            .data-list {
                .data-item {
                    & > p {
                        padding: 0;
                    }
                }
            }

            .data-list:nth-child(2) {
                .data-item {
                    display: flex;
                    align-items: center;

                    height: 50px;

                    background-color: rgb(249, 249, 249);

                    .item-info {
                        width: 89px;
                    }

                    .item-info:nth-child(2) {
                        width: 605px;
                        height: 100%;
                        line-height: 30px;

                        overflow: hidden;
                        cursor: help;
                        & > p {
                            word-break: keep-all;
                        }
                    }

                    .item-info:nth-child(3) {
                        width: 99px;
                    }

                    .item-info:nth-child(4) {
                        display: flex;
                        align-items: center;
                        justify-content: space-around;

                        width: 120px;

                        .item-button {
                            width: 46px;
                            height: 27px;
                            line-height: 27px;

                            background-color: rgb(102, 204, 255);
                            border-radius: 4px;
                            border: solid 1px rgb(102, 153, 204);
                            cursor: pointer;
                        }

                        .item-button:last-child {
                            background-color: rgb(255, 204, 204);
                            border: solid 1px rgb(255, 153, 204);
                        }
                    }
                }

                .data-item:nth-child(1) {
                    height: 30px;

                    background-color: rgb(249, 249, 249);

                    & > p {
                        width: 100%;

                        text-align: center;
                    }
                }

                .data-item:nth-child(2) {
                    background-color: rgb(228, 228, 228);

                    .item-info:nth-child(4) {
                        justify-content: center;
                    }
                }
            }
        }
    }

    .article-mask {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(55,55,55,.6);

        z-index: 1000;

        .article-content {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            margin: auto;

            width: 400px;
            height: 315px;

            border: solid 3px #cdcdcd;
            border-radius: 4px;

            background-color: #f2f2f2;

            z-index: 1001;

            .article-header {
                display: flex;
                justify-content: space-between;

                padding: 5px;

                background-color: #ddd;

                font-weight: bold;

                .modal-close {
                    cursor: pointer;
                }
            }

            .article-list {
                margin: 10px;

                border: solid 1px #ddd;
                border-radius: 4px;

                .article-item {
                    display: flex;
                    justify-content: space-between;

                    padding: 10px 0;

                    border-bottom: solid 1px #ddd;

                    .article-item-t {
                        flex-grow: 1;
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;

                        margin-right: 20px;
                        padding: 0 17px;

                        width: 70px;
                    }

                    .column-textarea {
                        margin-right: 10px;

                        width: 290px;
                    }
                }

                .article-item:last-child {
                    justify-content: center;

                    border: none;
                }
            }
        }

        .edit-article-content {
            height: 390px;
        }
    }
}
</style>